*{
    box-sizing: border-box;
}
html,body{
    height: 100%;
    width: 100%;
    margin: 0px;
}

.chat{
    height: 100%;
    width: 100%;
}

.chat-heading{
    text-align: center;
    margin: 0px;
    height: 5%;
    background-color: lightsalmon;
}

.message-wrap{
    height: 85%;
    width: 100%;
    overflow: auto;
    background-color: gainsboro;
    padding-left: 10px;
    padding-right: 10px;
}

.input-box{
    height: 5%;
    width: 100%;
    font-size: 20px;
}

.send{
    float: right;
}

.name-msg-wrap{
    max-width: 65%;
    height: auto;
    background-color: lightgreen;
    padding: 10px;
    width: fit-content;
}

.msg-box{
    overflow: auto;
    height: auto;
    width: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
}

.msg-wrap{
    overflow-wrap: break-word;
    font-family: cursive;
    color: brown;
}

.name-wrap{
    font-family: monospace;
    color: blue;
}

#root{
    margin-left: 10%;
    margin-right: 10%;
    height: 100%;
}